.auth-buttons {
    color: blue;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:  rgb(81, 104, 170);
    color: white;
    padding: 10px 20px;
    
}

.menu-icon {
    font-size: 24px;
    cursor: pointer;
    
}
.menu-icon:hover{
   background-color:rgb(48, 45, 45);
}

h1 {
    margin: 0;
    
}

.auth-buttons a {
    color: white;
    text-decoration: none;
    margin-left: 10px;
}

.side-menu {
    height: 100%;
    width: 0; /* Start hidden */
    position: fixed; /* Fix position */
    top: 0px;
    left: 0;
    background-color: rgb(81, 104, 170);
    overflow-x: hidden;
    transition: 0.3s; /* Smooth transition */
    padding-top: 20px;
    z-index: 1000; /* Ensure it stays on top */
}

.close-button{
    margin-left: 10px;
}

.side-menu a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 18px;
    color: white;
    display: block;
    transition: 0.5s;
}

.side-menu a:hover {
    background-color: #575757;
}
#signlog
{
    transition: 0.5s;
}
#signlog:hover
{
    
    background-color: #000000;
}